  justify-content: space-between;
}

.games__day {
  display: flex;
  flex-direction: column;
}
.games__day:not(:last-child) {
  margin-bottom: 10px;
}
.games__day span {
  padding-bottom: 10px;
  width: 64px;
}
.games__day time {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 21px;
  border-top: 1px solid #ffffff;
}
.games__day b {
  font-size: 40px;
  font-weight: 700;
  line-height: 47px;
  padding-right: 7px;
}

.games__heading {
  padding: 0;
  margin: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 82px;
  line-height: 79px;
  letter-spacing: 0.03em;
  text-align: center;
  width: 205px;
  text-transform: uppercase;
}

.games__description {
  padding: 0;
  margin: 0;
  font-size: 18px;
  line-height: 27px;
  text-align: center;
  margin-bottom: 45px;
}

.games__link {
  font-weight: 700;
  font-size: 12px;
  display: flex;
  justify-content: center;
  line-height: 14px;
  align-items: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  width: 347px;
  min-height: 71px;
  text-align: center;
  color: #ffffff;
  border: 4px solid #ffffff;
  cursor: pointer;
  opacity: 1;
}
.games__link:hover, .games__link:active {
  transition: opacity 0.3s ease;
}

.games__img {
  width: 100%;
  height: 100%;
  aspect-ratio: 660/500;
}

.main__intro {
  margin-bottom: 120px;
}

.main__about {
  margin: 120px 0 235px;
}

.main__product {
  margin-bottom: 120px;
}

.main__games {
  margin-bottom: 105px;
}

